    color: green;
    background: lightgrey;
    border-style: solid;
    border-radius: 5px;
    font-size: 20px;
    padding: 5px;
    margin-bottom: 10px;
}
.improvedError{
    color: red;
    background: lightgrey;
    border-style: solid;
    border-radius: 5px;
    font-size: 20px;
    padding: 5px;
    margin-bottom: 10px;
}